import React, { useState, useEffect } from 'react';
import Sidebar from './components/Sidebar';
import {
  PieChart, Pie, Cell, BarChart, Bar, XAxis, YAxis, Tooltip, ResponsiveContainer
} from 'recharts';
import { useNavigate, useParams } from 'react-router-dom';
import { FiArrowLeft } from 'react-icons/fi';
import apiRequest from './components/Requests'; // путь обнови под свой проект
import './styles/pollviewcreator.css';

const PollViewCreator = () => {
  const navigate = useNavigate();
  const { poll_id } = useParams();
  const [pollData, setPollData] = useState(null);
  const [chartType, setChartType] = useState('pie');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchPoll = async () => {
      try {
        const response = await apiRequest({
          url: `http://83.166.244.206/get-poll/${poll_id}`,
          method: 'GET',
          auth: true,
        });

        if (!response.ok) {
          throw new Error('Ошибка загрузки данных');
        }

        const data = await response.json();
        console.log("Получены данные:", data);

        const currentUsername = localStorage.getItem('username');

        if (data.user_username === currentUsername) {
          setPollData(data);
          return;
        }

        if (!data.options || typeof data.options !== 'object') {
          throw new Error('Неверный формат данных опроса');
        }

        setPollData(data);
      } catch (err) {
        console.error(err);
        setError('Не удалось загрузить данные опроса');
      } finally {
        setLoading(false);
      }
    };

    fetchPoll();
  }, [poll_id, navigate]);

  if (loading) return <div className="loading">Загрузка...</div>;
  if (error) return <div className="error">{error}</div>;
  if (!pollData || !pollData.options) return <div className="error">Опрос не найден или нет данных</div>;

  const data = Object.entries(pollData.options).map(([name, value], idx) => {
    const colors = ['#4F46E5', '#10B981', '#FACC15', '#F87171', '#60A5FA', '#F472B6'];
    return {
      name,
      value,
      color: colors[idx % colors.length],
    };
  });

  const totalVotes = data.reduce((sum, item) => sum + item.value, 0);

  return (
    <div className="main-layout">
      <Sidebar />
      <div className="poll-view-content">
        <button className="back-to-home-button" onClick={() => navigate(-1)}>
          <FiArrowLeft size={30} />
        </button>

        <div className="poll-header">
          <div className="poll-icon" />
          <h1 className="poll-title">{pollData.name}</h1>
        </div>
        <p className="poll-description">{pollData.description}</p>

        <div className="poll-stats">
          <div>Голосов: <strong>{totalVotes}</strong></div>
        </div>

        <div className="poll-body">
          <div className="options-list">
            {data.map((option, i) => (
              <div className="option-item" key={i}>
                <span className="option-color" style={{ backgroundColor: option.color }} />
                <span>{option.name}</span>
                <span><strong>{totalVotes > 0 ? ((option.value / totalVotes) * 100).toFixed(0) : 0}%</strong></span>
              </div>
            ))}
          </div>

          <div className="chart-container">
            <ResponsiveContainer width="100%" height={300}>
              {chartType === 'pie' ? (
                <PieChart>
                  <Pie data={data} dataKey="value" innerRadius={60} outerRadius={100}>
                    {data.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={entry.color} />
                    ))}
                  </Pie>
                  <Tooltip />
                </PieChart>
              ) : (
                <BarChart data={data}>
                  <XAxis dataKey="name" />
                  <YAxis />
                  <Tooltip />
                  <Bar dataKey="value">
                    {data.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={entry.color} />
                    ))}
                  </Bar>
                </BarChart>
              )}
            </ResponsiveContainer>
          </div>
        </div>

        <div className="controls">
          <div className="view-switch">
            <button onClick={() => setChartType('pie')} className={chartType === 'pie' ? 'active' : ''}>Круговая</button>
            <button onClick={() => setChartType('bar')} className={chartType === 'bar' ? 'active' : ''}>Столбчатая</button>
          </div>
          <button className="export-button">Export</button>
        </div>
      </div>
    </div>
  );
};

export default PollViewCreator;
