import React, { useEffect, useState } from 'react';
import './styles/profile.css';
import Sidebar from "./components/Sidebar";
import { Link } from 'react-router-dom';
import apiRequest from './components/Requests';

const ProfilePage = () => {
  const [polls, setPolls] = useState([]);
  const [loading, setLoading] = useState(true);

  const username = localStorage.getItem('username');
  const userId = localStorage.getItem('id');
  const name = localStorage.getItem('name');
  const description = localStorage.getItem('description');
  const avatarUrl = `http://83.166.244.206/get-profile-picture/${userId}`;

  useEffect(() => {
    const fetchPolls = async () => {
      try {
        const response = await apiRequest({
          url: `http://83.166.244.206/get-poll-by-user/${username}`,
          method: 'GET',
          auth: true,
        });

        if (!response.ok) {
          throw new Error('Failed to fetch polls');
        }

        const data = await response.json();
        setPolls(data);
      } catch (error) {
        console.error('Error loading polls:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchPolls();
  }, [username]);

  const formatPollOptions = (options) => {
    const totalVotes = Object.values(options).reduce((sum, val) => sum + val, 0);

    const colorClasses = ['yellow', 'green', 'blue', 'red', 'purple', 'pink', 'orange'];

    return Object.entries(options).map(([option, count], index) => {
      const percent = totalVotes === 0 ? 0 : Math.round((count / totalVotes) * 100);
      const colorClass = colorClasses[index % colorClasses.length];

      return (
        <div key={option} className={`vote-option ${colorClass}`}>
          {option} – {percent}%
        </div>
      );
    });
  };

  const isOpen = (endDate) => new Date(endDate) > new Date();

  return (
    <div className='main-layout'>
      <Sidebar />
      <div className="profile-content">
        <div className="profile-header">
          <img src={avatarUrl} alt="Avatar" className="profile-avatar" />
          <div className="profile-info">
            <h2>{name}</h2>
            <p>{description}</p>
            <div className="profile-stats">
              <div>Created: {polls.length}</div>
              <div>Participated: 8</div>
            </div>
          </div>
        </div>

        <div className="profile-tabs">
          <button className="tab-btn active">Your Polls</button>
          <button className="tab-btn">Participated</button>
        </div>

        <div className="votes-grid">
          <Link to='/new-poll'>
            <div className="vote-card create-card">
              <div className="create-icon">
                <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 4v16m8-8H4" />
                </svg>
              </div>
              <div className="create-text">Create Poll</div>
            </div>
          </Link>

          {loading ? (
            <p>Loading...</p>
          ) : polls.length === 0 ? (
            <p>You haven't created any polls yet.</p>
          ) : (
            polls.map((poll) => (
              <div key={poll.id} className="vote-card">
                <div className="vote-header">
                  <img src={avatarUrl} alt="User" className="vote-avatar" />
                  <span className={`vote-status ${isOpen(poll.end_date) ? 'open' : 'closed'}`}>
                    {isOpen(poll.end_date) ? 'Open' : 'Closed'}
                  </span>
                </div>
                <div className="vote-body">
                  <h3>{poll.name}</h3>
                  <p>Total votes: {poll.votes}</p>
                  <div className="vote-results">
                    {formatPollOptions(poll.options)}
                  </div>
                </div>
              </div>
            ))
          )}
        </div>
      </div>
    </div>
  );
};

export default ProfilePage;
